import { itemActionTypes } from '../actionTypes';

const INITIAL_STATE = {
  itemsList: { items: [], totalResult: null, error: null, loading: false },
  activeItem: { item: null },
};

export default (state = INITIAL_STATE, action) => {
  let error;
  switch (action.type) {
    case itemActionTypes.FETCH_ITEMS:
    case itemActionTypes.FETCH_ITEMS_STORAGE:
      return {
        ...state,
        itemsList: { items: [], totalResult: null, error: null, loading: true }
      };
    case itemActionTypes.FETCH_ITEMS_SUCCESS:
    case itemActionTypes.FETCH_ITEMS_STORAGE_SUCCESS:
      return {
        ...state,
        itemsList: { ...action.payload, error: null, loading: false }
      };
    case itemActionTypes.FETCH_ITEMS_FAILURE:
    case itemActionTypes.FETCH_ITEMS_STORAGE_FAILURE:
      error = action.payload || { message: action.payload.message };
      return {
        ...state,
        itemsList: { items: [], totalResult: null, error, loading: false }
      };
    case itemActionTypes.RESET_ITEMS:
      return {
        ...state,
        itemsList: { items: [], totalResult: null, error: null, loading: false }
      };

    case itemActionTypes.SET_ACTIVE_ITEM:
      return {
        ...state,
        activeItem: { item: action.payload }
      };
    case itemActionTypes.RESET_ACTIVE_ITEM:
      return {
        ...state,
        activeItem: { item: null }
      };

    case itemActionTypes.UPDATE_ITEMS:
      return {
        ...state,
        itemsList: {
          ...state.itemsList,
          items: !!state.itemsList.items && state.itemsList.items
            .map(item => {
              const updatedItem = action.payload.find(
                updatedData => encodeURI(item.nasaId) === updatedData.nasaId
              );
              if (!!updatedItem) {
                return { ...item, ...updatedItem, nasaId: item.nasaId }
              }
              return item
            })
        }
      };

    case itemActionTypes.UPDATE_SINGLE_ITEM:
      return {
        ...state,
        itemsList: {
          ...state.itemsList,
          items: !!state.itemsList.items && state.itemsList.items
            .map(item => {
              if (item.nasaId === action.payload.nasaId) {
                return { ...item, ...action.payload }
              }
              return item
            })
        }
      };

    case itemActionTypes.DELETE_SINGLE_ITEM:
      return {
        ...state,
        itemsList: {
          ...state.itemsList,
          items: !!state.itemsList.items && state.itemsList.items
            .filter(item => item.nasaId !== action.payload)
        }
      };

    default:
      return state;
  }
};