import React, { Component } from 'react';
import { reduxForm, Field } from 'redux-form';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons';

import Button from '../Button';
import Popup from '../Popup';
import { Input, Textarea } from '../FormControl';

import './Form.scss';

function validate(values) {
  const errors = {};

  if (!values.type || values.type.trim() === '') {
    errors.type = 'Required';
  }
  if (!values.imageUrl || values.imageUrl.trim() === '') {
    errors.imageUrl = 'Required';
  }
  if (!values.videoUrl || values.videoUrl.trim() === '') {
    errors.videoUrl = 'Required';
  }

  return errors;
}

class Form extends Component {
  static renderError(newItem) {
    if (newItem && newItem.error && newItem.error.message) {
      return (
        <div className="alert alert-danger">
          {newItem ? newItem.error.message : ''}
        </div>
      );
    }
    return <span />;
  }

  closePopup() {
    const { unSelectItem, isAddedToCollection } = this.props;
    if (isAddedToCollection) {
      //
    } else {
      unSelectItem();
    }
  }

  render() {
    const {
      handleSubmit,
      updateItem,
      addItemToNasaCollection,
      isAddedToCollection
    } = this.props;

    return (
      <Popup onClosePopup={() => this.closePopup()}>
        <div className="form">
          <div className="form__header">
            <div className="form__header__title">
              {isAddedToCollection ? 'Edit' : 'Add to collection'}
            </div>
          </div>
          <form
            onSubmit={handleSubmit(isAddedToCollection ? updateItem : addItemToNasaCollection)}
            className="form__body"
          >
            <Field
              id="itemFormTitle"
              name="title"
              type="text"
              size="small"
              component="input"
              className="input form-control form-control--small"
            />
            <Field
              id="itemFormDescription"
              name="description"
              size="small"
              component="textarea"
              rows="8"
              className="textarea form-control form-control--small"
            />
            <Field
              id="itemFormType"
              name="type"
              size="small"
              component="select"
              className="select form-control form-control--small"
            >
              <option value="video">Video</option>
              <option value="image">Image</option>
            </Field>
            <Field
              id="itemFormImage"
              name="imageUrl"
              type="text"
              size="small"
              component="input"
              className="input form-control form-control--small"
            />
            <Field
              id="itemFormVideo"
              name="videoUrl"
              type="text"
              size="small"
              component="input"
              className="input form-control form-control--small"
            />
            <Button type="submit" onClick={() => this.closePopup()}>
              <FontAwesomeIcon icon={faCheck} />&nbsp;
              {isAddedToCollection ? 'Save' : 'Add to collection'}
            </Button>
          </form>
        </div>
      </Popup>
    );
  }
}

export default reduxForm({
  form: 'Form',
  validate
})(Form);
