import { combineEpics, ofType } from 'redux-observable';
import { of, from } from 'rxjs';
import { map, flatMap, catchError } from 'rxjs/operators';

import { itemActionTypes } from '../actionTypes';
import { itemActions } from '../actions';

import { search, getAssets } from '../../utils/nasaApi';
import { findMP4Url, findPNGUrl } from '../../utils/common';
import { getItems } from '../../utils/storage';

const fetchItemsEpic = action$ => action$.pipe(
  ofType(itemActionTypes.FETCH_ITEMS),
  flatMap(action => from(search(action.payload)).pipe(
    map((collection) => {
      let items = collection.items;
      let totalResult = collection.metadata.total_hits;
      return itemActions.fetchItemsSuccessAction(items, totalResult);
    }),
    catchError(error => of(itemActions.fetchItemsFailureAction(error)))
  ))
);

const fetchItemsStorageEpic = action$ => action$.pipe(
  ofType(itemActionTypes.FETCH_ITEMS_STORAGE),
  flatMap(() => of(getItems()).pipe(
    map((items) => {
      return itemActions.fetchItemsStorageSuccessAction(items);
    }),
    catchError(error => of(itemActions.fetchItemsStorageFailureAction(error)))
  ))
);

const getItemAssetsEpic = action$ => action$.pipe(
  ofType(itemActionTypes.FETCH_ITEMS_SUCCESS),
  flatMap(action => from(Promise.all(action.payload.items.map(item => getAssets(item.nasaId)))).pipe(
    map((collections) => {
      let updatedItems = collections.map(collection => ({
        nasaId: collection.collection && collection.collection.href.split('https://images-api.nasa.gov/asset/')[1] || null,
        videoUrl: collection.collection && findMP4Url(collection.collection.items),
        imageUrl: collection.collection && findPNGUrl(collection.collection.items) || 'https://dumielauxepices.net/sites/default/files/black-860221-5629409.jpg'
      }))
      return itemActions.updateItemsAction(updatedItems);
    }),
    catchError(error => of(itemActions.updateItemsFailureAction(error)))
  ))
);

export default combineEpics(
  fetchItemsEpic,
  fetchItemsStorageEpic,
  getItemAssetsEpic
);