import React, { PureComponent } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faTrashAlt, faPen, faHeart } from '@fortawesome/free-solid-svg-icons';
import { faHeart as faHeartEmpty } from '@fortawesome/free-regular-svg-icons';

import Button from '../Button';
import Item from './Item';
import Form from '../Form';

import './Collection.scss';

class Collection extends PureComponent {

  componentWillUnmount() {
    this.props.resetItems();
  }

  addToFavorite(item) {
    this.props.updateItem({
      ...item,
      isFavorite: !item.isFavorite,
    })
  }

  deleteItem(item) {
    this.props.deleteItem(item)
  }

  renderActionButtons(item) {
    const { isAddedToCollection, selectItem } = this.props;
    if (isAddedToCollection) {
      return (
        <div>
          <Button
            onClick={() => this.addToFavorite(item)}
            style={{color: item.isFavorite ? 'red' : 'initial'}}
            className="text--small" variant="default">
            <FontAwesomeIcon icon={item.isFavorite ? faHeart : faHeartEmpty} /> <span className="color--gray"></span>
          </Button>&nbsp;

          <Button
            onClick={() => this.deleteItem(item)}
            className="text--small" variant="default">
            <FontAwesomeIcon icon={faTrashAlt} /> <span className="color--gray"></span>
          </Button>&nbsp;

          <Button 
            onClick={() => selectItem(item)}
            className="text--small" variant="default">
            <FontAwesomeIcon icon={faPen} /> <span className="color--gray"></span>
          </Button>
        </div>
      )
    } else {
      const { selectItem } = this.props;
      return (
        <div>
            <Button onClick={() => selectItem(item)} className="text--small" variant="default" block>
              <FontAwesomeIcon icon={faPlus} /> <span className="color--gray">Add to NASA collection</span>
            </Button>
        </div>
      )
    }
  }

  renderItemList(items) {
    return items.map((item, index) => {
      return (
        <div className="collection__item" key={index}>
          <Item
            title={item.title}
            description={item.description}
            creator={item.creator}
            dateCreated={item.dateCreated}
            nasaId={item.nasaId}
            imageUrl={item.imageUrl}
            type={item.type}>
            {this.renderActionButtons(item)}
          </Item>
        </div>
      )
    });
  }

  render() {
    const { items, isSetActiveItem, isAddedToCollection } = this.props;

    return (
      <div className="collection">
        {items ? this.renderItemList(items) : ''}
        {isSetActiveItem ? <Form isAddedToCollection={isAddedToCollection}/> : ''}
      </div>
    );
  }
}

export default Collection;