import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

import Button from '../../components/Button';
import CollectionContainer from '../../components/Collection';

import './CollectionPage.scss';

export class CollectionPage extends Component {
  componentWillMount() {
    this.props.fetchItems();
  }

  render() {
    return (
      <div className="collection-page">
        <div className="collection-page__header">
          <div className="collection-page__header__title">
            <label>NASA Collection</label>
          </div>
          <div className="collection-page__header__actions">
            <Link to="/nasa-search">
              <Button>
                <FontAwesomeIcon icon={faPlus} /> Add new item
              </Button>
            </Link>
          </div>
        </div>
        <div className="collection-page__body">
          <CollectionContainer isAddedToCollection={true} />
        </div>
      </div>
    )
  }
};