import { connect } from 'react-redux';
import { itemActions } from '../../data/actions';

import { CollectionPage } from './CollectionPage';

const mapStateToProps = state => ({
  totalResult: state.item.itemsList.totalResult,
  loading: state.item.itemsList.loading
});

const mapDispatchToProps = dispatch => ({
  fetchItems: () => {
    dispatch(itemActions.fetchItemsStorageAction());
  }
});

const CollectionPageContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(CollectionPage);

export default CollectionPageContainer;