import React, { Component } from 'react';
import ReactPlayer from 'react-player'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

import './VideoPage.scss';

export class VideoPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      videoUrl: null
    };

    const { nasaId } = props.match.params;
    fetch(`https://images-api.nasa.gov/asset/${nasaId}`)
      .then(response => response.json())
      .then(data => this.setState({videoUrl: data.collection.items[0].href}));
  }

  render() {
    const { videoUrl } = this.state;
    const { goBack } = this.props.history;

    return (
      <div className="video-page">
        <div className="video-page__header">
          <div className="video-page__header__title">
            <label>NASA video</label>
          </div>
          <div className="video-page__header__actions">
          <span onClick={goBack} className="color--white">
            <FontAwesomeIcon icon={faTimes} />
          </span>
          </div>
        </div>
        <div className="video-page__body">
          <ReactPlayer
              url={videoUrl}
              controls
              width="100%" height="100%"
              />
        </div>
      </div>
    )
  }
};