import { connect } from 'react-redux';
import { itemActions } from '../../data/actions';

import { SearchPage } from './SearchPage';

const mapStateToProps = state => ({
  totalResult: state.item.itemsList.totalResult,
  loading: state.item.itemsList.loading
});

const mapDispatchToProps = dispatch => ({
  fetchItems: (queryString) => {
    dispatch(itemActions.fetchItemsAction(queryString));
  }
});

const SearchPageContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchPage);

export default SearchPageContainer;