import { connect } from 'react-redux';

import { itemActions } from '../../data/actions';
import { addItem, updateItem } from '../../utils/storage';

import Form from './Form';

const mapStateToProps = state => ({
  initialValues: state.item.activeItem.item
});

const mapDispatchToProps = dispatch => ({
  unSelectItem: () => {
    dispatch(itemActions.resetActiveItemAction());
  },
  addItemToNasaCollection: (value) => {
    addItem(value);
  },
  updateItem: (value) => {
    updateItem(value);
    dispatch(itemActions.updateSingleItemAction(value));
  }
});

const FormContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(Form);

export default FormContainer;