/* eslint no-restricted-globals: ["error", "event"] */

const collectionKey = 'NASACollection';

function addItem(newItem) {
  let collection = JSON.parse(localStorage.getItem(collectionKey));
  if (!!collection) {
    let existItemIndex = -1;
    collection.findIndex(item => item.nasaId === newItem.nasaId);
    if (existItemIndex >= 0) {
      collection[existItemIndex] = newItem;
      localStorage.setItem(collectionKey, JSON.stringify(collection));
    } else {
      const newCollection = [...collection, newItem];
      localStorage.setItem(collectionKey, JSON.stringify(newCollection));
    }
  } else {
    const newCollection = [newItem];
    localStorage.setItem(collectionKey, JSON.stringify(newCollection));
  }
}

function updateItem(updateItem) {
  let collection = JSON.parse(localStorage.getItem(collectionKey));
  if (!!collection) {
    let existItemIndex = collection.findIndex(item => item.nasaId === updateItem.nasaId);
    if (existItemIndex >= 0) {
      collection[existItemIndex] = updateItem;
      localStorage.setItem(collectionKey, JSON.stringify(collection));
    }
  }
}

function getItem(nasaId) {
  let collection = JSON.parse(localStorage.getItem(collectionKey));
  if (!!collection) {
    let existItem = collection.find(item => item.nasaId === nasaId);
    return existItem;
  }
  return null;
}

function getItems() {
  return JSON.parse(localStorage.getItem(collectionKey));
}
 
function deleteItem(nasaId) {
  var result = confirm('Are you sure?');
  if (!result) return;
  let collection = JSON.parse(localStorage.getItem(collectionKey));

  if (!collection) return;
  let existItemIndex = collection.findIndex(item => item.nasaId === nasaId);

  if (existItemIndex < 0) return;
  collection.splice(existItemIndex, 1);
  localStorage.setItem(collectionKey, JSON.stringify(collection));
}

export {
  addItem,
  updateItem,
  getItem,
  getItems,
  deleteItem
}