import { itemActionTypes } from '../actionTypes';

// Fetch Items
export const fetchItemsAction = (queryString) => ({
  type: itemActionTypes.FETCH_ITEMS,
  payload: queryString
});

export const fetchItemsSuccessAction = (items, totalResult) => ({
  type: itemActionTypes.FETCH_ITEMS_SUCCESS,
  payload: {items, totalResult}
});

export const fetchItemsFailureAction = error => ({
  type: itemActionTypes.FETCH_ITEMS_FAILURE,
  payload: error
});

// Fetch Items
export const fetchItemsStorageAction = () => ({
  type: itemActionTypes.FETCH_ITEMS_STORAGE
});

export const fetchItemsStorageSuccessAction = items => ({
  type: itemActionTypes.FETCH_ITEMS_STORAGE_SUCCESS,
  payload: {items}
});

export const fetchItemsStorageFailureAction = error => ({
  type: itemActionTypes.FETCH_ITEMS_STORAGE_FAILURE,
  payload: error
});

// Reset Items
export const resetItemsAction = () => ({
  type: itemActionTypes.RESET_ITEMS
});

// Active Item
export const setActiveItemAction = (item) => ({
  type: itemActionTypes.SET_ACTIVE_ITEM,
  payload: item
});

export const resetActiveItemAction = () => ({
  type: itemActionTypes.RESET_ACTIVE_ITEM
});

// Update Items
export const updateItemsAction = items => ({
  type: itemActionTypes.UPDATE_ITEMS,
  payload: items
});

// Update Single Item
export const updateSingleItemAction = item => ({
  type: itemActionTypes.UPDATE_SINGLE_ITEM,
  payload: item
});

// Delete Single Item
export const deleteSingleItemAction = nasaId => ({
  type: itemActionTypes.DELETE_SINGLE_ITEM,
  payload: nasaId
});
  