function parseItem(item) {
  let parsedData;
  if (item.data && item.data.length) {
    parsedData = {
      nasaId: item.data[0].nasa_id,
      title: item.data[0].title,
      description: item.data[0].description,
      creator: item.data[0].center,
      dateCreated: item.data[0].date_created,
      type: item.data[0].media_type,
    };
  } else {
    parsedData = {
      nasaId: null,
      title: null,
      description: null,
      creator: null,
      dateCreated: null,
      imageUrl: null,
      type: null,
    }
  }

  let imageUrl;
  if (item.links && item.links.length && parsedData.type === 'image') {
    let image = item.links
      .find(link => link.render === 'image');
    imageUrl = !!image ? image.href : null;
  }

  return {
    ...parsedData,
    imageUrl
  };
}

function search(queryString) {
  return fetch(`https://images-api.nasa.gov/search?q=${queryString}&media_type=video`)
    .then(response => response.json())
    .then(response => {
      return {
        links: response.collection.links,
        metadata: response.collection.metadata,
        items: response.collection.items.map(item => parseItem(item)),
      };
    });
}

function getAssets(nasaId) {
  return fetch(`https://images-api.nasa.gov/asset/${nasaId}`)
    .then(response => response.json());
}

export {
  search,
  getAssets
};