import React, { PureComponent } from 'react';
import Truncate from 'react-truncate';
import Moment from 'react-moment';

import Thumbnail from './Thumbnail';

import './Item.scss';

class Item extends PureComponent {
  render() {
    const {
      children,
      title, description, creator, dateCreated,
      nasaId, imageUrl, type
    } = this.props;

    return (
      <div className="item">
        <Thumbnail nasaId={nasaId} imageUrl={imageUrl} type={type} />
        <div className="item__sub-title text--xsmall color--dark-gray">
          <div className="item__sub-title__media-type">{creator}</div>
          <div className="item__sub-title__date-created">
            <Moment format="DD MMM, YYYY">{dateCreated}</Moment>
          </div>
        </div>
        <div className="item__title text--small">{title}</div>
        <div className="item__description text--xsmall text--justify color--dark-gray">
          <Truncate lines={3} ellipsis="...">{description}</Truncate>
        </div>
        <div className="item__actions text--xsmall color--dark-gray">
          { children }
        </div>
      </div>
    );
  }
}

export default Item;