import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleLeft } from '@fortawesome/free-solid-svg-icons';

import CollectionContainer from '../../components/Collection';
import { Input } from '../../components/FormControl';

import './SearchPage.scss';

export class SearchPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      queryString: null,
    };
  }

  search(event) {
    if (event.key === 'Enter') {
      this.setState({queryString: event.target.value}, () => {
        this.props.fetchItems(this.state.queryString);
      });
    }
  }

  render() {
    const { queryString } = this.state;
    const { totalResult, loading } = this.props;

    return (
      <div className="search-page">
        <div className="search-page__header">
          <div className="search-page__header__actions">
            <Link to="/" className="text--small">
              <FontAwesomeIcon icon={faAngleLeft} /> Back to Collection
            </Link>
          </div>
          <div className="search-page__header__title">
            <label>Search from Nasa</label>
          </div>
          <div className="search-page__header__input">
            <Input
              type="text"
              placeholder="Type something then Enter to search..."
              onKeyPress={(event) => this.search(event)}
            />
          </div>
        </div>
        <div className="search-page__body">
          <div className="search-page__body__total-hits">
            {
              totalResult !== null ? (
                <span>{ totalResult } result for "{ queryString }"</span>
              ) : ''
            }
            {
              loading ? (
                <span>loading...</span>
              ) : ''
            }
          </div>
          <CollectionContainer isAddedToCollection={false} />
        </div>
      </div>
    )
  }
};