import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { Provider } from 'react-redux';

import { CollectionPage, SearchPage, EditPage, VideoPage } from './app/pages';
import makeStore from './app/data';

import './app/styles/style.scss';

const store = makeStore();

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {

    return (
      <Provider store={store}>
        <Router>
          <div>
            <Route exact path="/" component={CollectionPage} />
            <Route path="/nasa-search" component={SearchPage} />
            <Route path="/video/:nasaId" component={VideoPage} />
              <Route path="/edit/:nasaId" component={EditPage} />
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
