import { connect } from 'react-redux';
import { itemActions } from '../../data/actions';
import { updateItem, deleteItem } from '../../utils/storage';

import Collection from './Collection';

const mapStateToProps = state => ({
  isSetActiveItem: !!state.item.activeItem.item,
  items: state.item.itemsList.items
});

const mapDispatchToProps = dispatch => ({
  selectItem: (item) => {
    dispatch(itemActions.setActiveItemAction(item));
  },
  updateItem: (item) => {
    updateItem(item);
    dispatch(itemActions.updateSingleItemAction(item));
  },
  deleteItem: (item) => {
    deleteItem(item.nasaId);
    dispatch(itemActions.deleteSingleItemAction(item.nasaId));
  },
  resetItems: () => {
    dispatch(itemActions.resetItemsAction());
  }
});

const CollectionContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(Collection);

export default CollectionContainer;