function findPNGUrl(items) {
  let image = items.find(item => item.href.endsWith('~small_thumb_00003.png'))
  return !!image ? image.href : null;
}

function findMP4Url(items) {
  let video = items.find(item => item.href.endsWith('~small.mp4'))
  return !!video ? video.href : null;
}

export {
  findPNGUrl,
  findMP4Url
};